.chestAnimation {
    animation-name: shake;
    animation-duration: 1.0s;
    animation-timing-function: linear;
}

@keyframes shake {
    0% {
        transform: translate(0px, 0em);
    }

    8% {
        transform: translate(0px, -1.5em);
    }

    16% {
        transform: translate(0px, -1.5em);
    }

    28% {
        transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    36% {
        transform: translate(0px, -1.5em) rotateZ(-5deg);
    }              

    44% {
        transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    52% {
        transform: translate(0px, -1.5em) rotateZ(-5deg);
    }

    60% {
        transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    68% {
        transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    80% {
        transform: translate(0px, -1.5em);
    }

    92% {
        transform: translate(0px, -1.5em);
    }

    100% {
        transform: translate(0px, 0em);
    }
}