html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

.App {
  height: 100%;
  display: flex;
  flex-flow: column;
  padding: 16px;
  text-align: center;
}

.chestAnimation {
    -webkit-animation-name: shake;
            animation-name: shake;
    -webkit-animation-duration: 1.0s;
            animation-duration: 1.0s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes shake {
    0% {
        -webkit-transform: translate(0px, 0em);
                transform: translate(0px, 0em);
    }

    8% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    16% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    28% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    36% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(-5deg);
                transform: translate(0px, -1.5em) rotateZ(-5deg);
    }              

    44% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    52% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(-5deg);
                transform: translate(0px, -1.5em) rotateZ(-5deg);
    }

    60% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    68% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    80% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    92% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    100% {
        -webkit-transform: translate(0px, 0em);
                transform: translate(0px, 0em);
    }
}

@keyframes shake {
    0% {
        -webkit-transform: translate(0px, 0em);
                transform: translate(0px, 0em);
    }

    8% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    16% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    28% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    36% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(-5deg);
                transform: translate(0px, -1.5em) rotateZ(-5deg);
    }              

    44% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    52% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(-5deg);
                transform: translate(0px, -1.5em) rotateZ(-5deg);
    }

    60% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    68% {
        -webkit-transform: translate(0px, -1.5em) rotateZ(5deg);
                transform: translate(0px, -1.5em) rotateZ(5deg);
    }

    80% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    92% {
        -webkit-transform: translate(0px, -1.5em);
                transform: translate(0px, -1.5em);
    }

    100% {
        -webkit-transform: translate(0px, 0em);
                transform: translate(0px, 0em);
    }
}
